.robot {
	&_container {
		position: relative; }
	&__img {
		position: absolute;
		top: -135px;
		left: -40px;
		pointer-events: none;
		@include nosel;
		z-index: -1;
		@include md {
			width: 826px; }
		@include sm {
			display: none; } }
	&__heading {
		max-width: 660px;
		text-align: center;
		margin: 118px 45px 0 auto;
		line-height: 1.2;
		padding: 0 30px;
		@include md {
			max-width: 460px;
			margin-top: 80px;
			margin-right: 0; }
		@include sm {
			margin: 100px auto;
			padding: 0; }
		& h2 {
			@include f(72px, 600);
			@include md {
				@include f(48px, 600); }
			@include sm {
				@include f(36px, 600); } }
		& p {
			@include f(30px, 600);
			margin: 7px 0 20px 13px;
			line-height: 1.6;
			@include md {
				@include f(18px, 600); } } }
	&__button {
		@include md {
			min-width: 260px; } }
	&__tablet-wrapper {
		width: 100%;
		background: #fff;
		height: 720px;
		border-radius: 16px;
		margin-top: 210px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		z-index: -2;
		padding: 0 48px;
		@include md {
			margin-top: 134px;
			height: 472px; }
		@include sm {
			height: 300px;
			margin-top: 526px;
			padding: 0 20px;
			max-width: 500px;
			left: 50%;
			transform: translateX(-50%); }
		@include ms {
			height: 190px;
			margin-top: 520px; } }
	&__tablet-inner {
		max-width: 1074px;
		width: 100%;
		max-height: 624px;
		border-radius: 8px;
		background: $darkgray;
		display: flex;
		justify-content: center;
		align-items: center; }
	&__tablet-icon {
		@include vcenter;
		@include sm {
			& svg {
				width: 50%;
				height: 50%;
				position: relative;
				left: 50%;
				transform: translateX(-50%); } } }
	&__box1 {
		position: absolute;
		top: 6.2%;
		left: 7%;
		z-index: -2;
		@include md {
			left: -6%;
			top: 4%; }
		@include sm {
			display: none; } }
	&__box2 {
		position: absolute;
		top: 545px;
		right: 10%;
		z-index: -2;
		@include md {
			right: -4%;
			top: 340px; }
		@include sm {
			display: none; } } }
.robot__tablet-video {
	@include md {
		height: 420px; }
	@include sm {
			height: 280px; }
	@include ms {
		height: 170px; } }

.robot__img-small {
	display: none;
	@include sm {
		display: block;
		top: 5%; } }
