.header {
	&__container {
		display: flex;
		padding: 60px 0 0;
		max-width: 1060px;
		@include md {
			padding-top: 50px; }
		@include sm {
			padding-top: 20px;
			max-width: 700px; } }
	&__logo {
		display: flex;
		align-items: center;
		margin-left: 55px;
		@include md {
			margin-left: 20px; }
		& img {
			width: 44px;
			height: 44px;
			margin-right: 12px;
			@include md {
				width: 33px;
				height: 33px; } }
		& .title {
			line-height: 1;
			& h1 {
				@include f(24px, 600);
				@include md {
					@include f(18px, 600); }
				& span {
					color: #56ecf6; } }
			& p {}
			@include f(13px, 500);
			@include md {
				@include f(9.75px, 500); } } }
	&__menu {
		margin-left: auto;
		@include sm {
			// display: none
			margin: 0; } }
	&__lang {
		@include sm {
 }			// display: none
		@include hover {
			.lang__current {
				color: $darkblue;
				background: #fff; } } } }

.nav {
	display: flex;
	align-items: center;
	line-height: 3.3;
	& li {
		@include sm {
			width: 100%;
			border-bottom: 1px solid $darkblue;
			text-align: right; } }
	@include sm {
		display: none;
		flex-direction: column;
		position: absolute;
		width: 100%;
		background: $white;
		top: 0;
		left: 0;
		z-index: 1;
		padding-top: 50px; } }
.nav-active {
	display: flex; }
.navigation__item {
	margin-right: 34px;
	@include sm {
		display: block;
		color: $darkblue;
		width: 100%;
		padding-right: 20px; } }

.header__login {
	margin-left: 5px;
	margin-right: 8px;
	& .login_log {
		margin-right: 30px; }
	& .login_sign {
		display: inline-block;
		min-width: 120px;
		// max-height: 50px
		line-height: 1;
		text-align: center;
		padding: 13px;
		background: #fff;
		color: $darkblue;
		border-radius: 16px;
		@include md {
			min-width: 96px; } }
	@include sm {
		display: none; } }

.header__lang {
	position: relative;
	z-index: 2;
	top: 5px;
	margin-right: 10px;
	@include hover {
		.lang__list {
			opacity: 1;
			visibility: visible; }
		.lang__current {
			border-radius: 16px 16px 0 0; } }
	& .lang__current {
		min-width: 80px;
		text-align: center;
		display: inline-block;
		// max-height: 42px
		line-height: 1;
		padding: 13px;
		background: #fff;
		border-radius: 16px;
		color: $darkblue;
		@include md {
			background: none;
			color: $white;
			min-width: 100%; }
		@include hover {
			text-decoration: underline; } }
	& .lang__list {
		position: absolute;
		width: 100%;
		top: 70%;
		left: 0;
		opacity: 0;
		visibility: hidden;
		padding: 13px;
		background: #fff;
		border-radius: 0 0 16px 16px;
		text-align: center;
		display: inline-block;
		@include hover {
			text-decoration: underline; }
		& .lang_extra {
			color: $darkblue; } }
	@include sm {
		display: none; } }

.header__toggle {
	cursor: pointer;
	display: none;
	align-items: center;
	margin-left: auto;
	width: 24px;
	height: 24px;
	margin-right: 20px;
	@include sm {
		display: flex;
		z-index: 2; } }

.header__toggle-icon {
	position: relative;
	width: 100%;
	height: 2px;
	background-color: $white;
	&:before, &:after {
		content: '';
		position: absolute;
		width: 100%;
		height: 2px;
		left: 0;
		background-color: $white; }
	&:before {
		top: -7px; }
	&:after {
		bottom: -7px; } }

.header__toggle-icon_active {
	background-color: transparent;
	&:before {
		@include rotate(45);
		top: 0;
		background-color: $black; }
	&:after {
		@include rotate(-45);
		bottom: 0;
		background-color: $black; } }
